*{
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
body{
  font-family: 'Montserrat', sans-serif;
}
.banner{
  width: 100%;
  height: 100vh;
  background: url("./img/bg.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
  background-attachment: fixed;
}
.banner__content{
  display: flex;
  align-items: center;
  height: 100vh;
  color: #fff;
}
.conatiner{
  width: 1200px;
  max-width: 1200px;
  margin: 0 auto;
  
}
.banner__text{
width: 500px;
}
.banner__text h3 {
  font-size: 50px;
}
.banner__text h1 {
  font-size: 80px;
  line-height: 80px;
  font-weight: 900;
  margin-top: 20px;
}
.banner__text p {
  font-size: 25px;
  font-weight: 400;
  margin-top: 20px;
}
.navbar{
  position: absolute;
  top: 0;
  left:0;
  width: 100%;
 
}
.navbar__text{
  display: flex;
  height: 60px;
  align-items: center;
  width: 1200px;
  margin: 0 auto;
}

.logo{
  width: 40%;

}
.logo img{
  width: 220px;
}
.navbar__ul img{
  width: 60%;
}
.navbar__ul{
  list-style-type: none;
  width: 60%;
  display: flex;
  justify-content: flex-end;
  
}
.navbar__ul li a{
  text-decoration: none;
  color: #fff;
  padding: 0 15px;
  font-weight: 800;
  text-transform: uppercase;
}
.banner__btn{
  margin-top: 35px;
}
.btn{
  text-decoration: none;
  border: none;
  outline: none;
  color: #fff;
  font-weight: 700;
  border-radius: 30px;
  font-size: 12px;
  padding: 20px 40px;
}
.btn-smart{
  background: rgb(255, 69, 79);
}
.about{
  padding: 80px 0;
}
.p-25{
  padding: 45px;
}
.row{
  width: 100%;
  display: flex;
  flex-wrap: wrap;
}
.col-6{
  width: 50%;
}
.col-3{
  width: 25%;
}
.col-4{
  width: 33.33333333333333%;  
}
.about h3{
  color: grey;
  font-size: 30px;
  margin-top: 12px;
  font-weight: 700;
}
.about h1{
  font-style: 40px;
  color: #252525;
  font-weight: 700;
  line-height: 40px;
  margin-bottom: 15px;

}
.about p{
  font-style: 18px;
  color: #8b8b8b;
  font-weight: 300;
}
.about__btn{
  margin-top: 25px;

}
.about__img{
  width: 100%;
  overflow: hidden;
}
.about__img img{
  width: 80%;
 
}
.shop{
  width: 100%;
  background: url(./img/bg.jpg);
  height: 450px;
  background-attachment: fixed;
}
.shop__text{
  height: 450px;
  display: flex;
  align-items: center; 
  width: 400px;
  justify-content: center;
  margin: 0 auto;
  color: #fff;

}
.shop__text h1 {
  font-style: 40px;
  line-height: 60px;
  font-weight: 600;
  font-style: normal;
  letter-spacing: .45px;
} 
.shop__btn{
  margin-top: 35px;

}
.about__imgg img{
 width: 80%;
 margin-left: 80px;
}
.clients{
  width: 100%;
  padding: 100px 0;
  background: url("img/bg.jpg");
  background-repeat: no-repeat;
  background-size: cover;
  color: #fff;
  background-attachment: fixed;
}
.client{
  text-align: center;
}
.client h1{
  font-size: 45px;
}
.client p{
  font-size: 18px;
  font-weight: 400;
  margin-top: 10px;
}
.prices {
  width: 100%;
  padding: 80px 0;
}

.price {
  padding: 20px;
  text-align: center;
}
.price__img {
  width: 100%;
}

.price__img img {
  width: 250px;
}

.price__heading {
  margin-top: 20px;
  font-size: 25px;
}
.price__text {
  margin-top: 15px;
  color: #888888;
  font-size: 18px;
  font-weight: 300;
}

.price_rs {
  font-size: 30px;
  margin-top: 15px;
}

@media (max-width: 700px) {
  .navbar__text {
    display: flex;
    height: auto;
    flex-direction: column;
    align-items: center;
    width: 100%;
    margin: 0 auto;
  }
  .logo {
    margin-bottom: 20px;
    margin-top: 20px;
  }
  .container {
    width: 100%;
    padding: 0 30px;
  }
  .banner__text {
    width: auto;
  }
  .banner__text h3 {
    font-size: 25px !important;
  }
  .banner__text h1 {
    font-size: 40px;
    line-height: 60px;
    font-weight: 900;
    margin-top: 20px;
  }
  .banner__text p {
    font-size: 20px;
    font-weight: 400;
    margin-top: 20px;
  }
  .navbar__ul {
    width: 100%;
    justify-content: center;
  }
  .navbar__ul li a {
    text-decoration: none;
    color: #fff;
    padding: 0 10px;
    font-weight: 300;
    text-transform: uppercase;
  }
  .col-3,
  .col-4,
  .col-6 {
    width: 100%;
  }
  .client {
    margin: 20px 0;
  }
  .about h1 {
    font-size: 25px;
  }
  .shop h1 {
    font-size: 25px;
  }
  .menu h1 {
    font-size: 25px;
  }
}